<template>
    <div>
        <home-msg v-if="template" :template="template"></home-msg>
        <p v-else>没有template 参数</p>
    </div>
</template>

<script>
import { getJsonResult } from '@/utils/server-wrapper'
import HomeMsg from './components/HomeMsg'

export default {
    data() {
        return {
            template: null
        }
    },
    components: {
        HomeMsg
    },
    async beforeRouteEnter(to, from, next) {
        const templateId = to.query.id
        if (!templateId) {
            next()
            return
        }
        const template = await getJsonResult(`/productTemplate/getDetailById?id=${templateId}`)
        if (template.config && typeof template.config === 'string') {
            template.config = JSON.parse(template.config)
        }
        const items = []
        for (let raw of template.items) {
            if (raw) {
                if (typeof raw.value === 'string') {
                    raw.value = JSON.parse(raw.value)
                }
                items.push(raw)
            }
        }
        template.items = items
        next(vm => {
            vm.$data.template = template
        })
    }
}
</script>
